* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
html {
  scroll-behavior: smooth;
}
header {
  display: flex;
  justify-content: space-between;
}
h1 {
  font-weight: 300;
  font-size: 32px;
  line-height: 50px;
  color: white;
}
h2 {
  font-size: 36px;
  font-weight: 500;
  padding-bottom: 20px;
  line-height: 46px;
}
p {
  font-size: 20px;
  line-height: 26px;
  font-weight: 100;
  text-align: justify;
}
#logo {
  height: 50px;
  vertical-align: middle;
  margin-right: 30px;
}
#about ul {
  display: flex;
}
#about li {
  margin-left: 70px;
  line-height: 30px;
  padding: 10px 15px;
}
a {
  text-decoration: none;
  color: inherit;
  font-size: 18px;
  font-weight: 100;
}
li {
  list-style: none;
  color: white;
}
li#sb-contact {
  padding: 2px 0;
}
#sb-contact button {
  background: transparent;
  color: white;
  padding: 8px 30px;
  border: 1px solid white;
  margin-left: 190px;
  outline: none;
  line-height: 32px;
  font-size: 18px;
  font-weight: 100;
}
div.parts {
  width: 100%;
  background: linear-gradient(#141417, #151519);
}
div.partsContent {
  padding: 40px 50px;
  height: 100vh;
  color: white;
  width: 100%;
  max-width: 1450px;
  margin: auto;
}
#about main {
  width: 30%;
  padding: 20px 0;
  position: absolute;
  bottom: 25%;
}
.parts-background-image {
  background-image: url("./images/about-blue-logo.png");
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: auto 85%;
}
.servWrapper {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  height: Calc(100% - 190px);
}
#services p {
  font-size: 28px;
  line-height: 36px;
  margin: 20px 0;
}
.service {
  border: 1px solid white;
  border-radius: 50px;
  padding: 0 20px;
  min-width: 320px;
  width: 28%;
  min-width: 200px;
  height: 100%;
  overflow: hidden;
  vertical-align: middle;
  display: flex;
  align-items: center;
}
.service h2 {
  font-size: 46px;
  line-height: 60px;
  margin: 20px 0;
}
.part-title {
  font-size: 50px;
  padding: 25px 0;
  margin-bottom: 40px;
}
.servContent {
  margin: auto;
}
#contact main {
  display: flex;
  justify-content: space-between;
}
#contact form {
  width: 400px;
  display: flex;
  flex-direction: column;
}
#contact input,
#contact button,
#contact textarea {
  width: 100%;
  padding: 16px 15px;
  font-size: 24px;
  font-weight: 100;
  border: none;
  border-radius: 12px;
  display: block;
  margin-bottom: 30px;
  outline: none;
}
#contact button {
  background: #004dff;
  color: white;
  font-size: 20px;
  cursor: pointer;
}
#contact span {
  font-size: 18px;
  font-weight: 100;
  position: absolute;
  margin-top: -60px;
}
#contact ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 15%;
}
#contact li {
  padding-right: 50px;
  display: flex;
  font-size: 22px;
  line-height: 40px;
  font-weight: 100;
  margin-top: 10px;
}
#contact li img {
  height: 30px;
  margin: 5px;
}
#contact .partsContent {
  background-image: url("./images/contact-blue-logo.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 50%;
}
